export async function saveTags(body) {
  return new Promise((rej) => {
    rej({
      state: "00",
      data: body,
    });
  });
}

export function tag_view_tags() {
  return axios({
    headers: {
      "Access-Control-Allow-Origin": "*",
      "Content-Type": "application/json",
      "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE",
    },

    method: "GET",
    url: `${api}/tag_view/GetTag_List`,
    mode: "no-cors",
  });
}
